module.exports = "uniform float time;\nuniform sampler2D texture1;\nuniform sampler2D texture2;\nuniform float progress;\nvarying vec2 vUv;\nvec4 getFromColor(vec2 inUV) {\n\treturn texture2D(texture1, inUV);\n}\nvec4 getToColor(vec2 inUV) {\n\treturn texture2D(texture2, inUV);\n}\nuniform float persp;\nuniform float unzoom;\nuniform float reflection;\nuniform float floating;\nvec2 project(vec2 p) {\n\treturn (p * vec2(1.0, -1.2)) + vec2(0.0, -floating / 100.);\n}\nbool inBounds(vec2 p) {\n\treturn all(lessThan(vec2(0.0), p)) && all(lessThan(p, vec2(1.0)));\n}\nvec4 bgColor(vec2 p, vec2 pfr, vec2 pto) {\n\tvec4 c = vec4(0.0, 0.0, 0.0, 1.0);\n\tpfr = project(pfr);\n\tif (inBounds(pfr)) {\n\t\tc += mix(vec4(0.0), getFromColor(pfr), reflection * mix(1.0, 0.0, pfr.y));\n\t}\n\tpto = project(pto);\n\tif (inBounds(pto)) {\n\t\tc += mix(vec4(0.0), getToColor(pto), reflection * mix(1.0, 0.0, pto.y));\n\t}\n\treturn c;\n}\nvec2 xskew(vec2 p, float persp, float center) {\n\tfloat x = mix(p.x, 1.0 - p.x, center);\n\treturn ((vec2(x, (p.y - ((0.5 * (1.0 - persp)) * x)) / (1.0 + ((persp - 1.0) * x))) - vec2(0.5 - distance(center, 0.5), 0.0)) * vec2((0.5 / distance(center, 0.5)) * (center < 0.5 ? 1.0 : -1.0), 1.0)) + vec2(center < 0.5 ? 0.0 : 1.0, 0.0);\n}\nvec4 transition(vec2 op) {\n\tfloat uz = (unzoom * 2.0) * (0.5 - distance(0.5, progress));\n\tvec2 p = (-uz * 0.5) + ((1.0 + uz) * op);\n\tvec2 fromP = xskew((p - vec2(progress, 0.0)) / vec2(1.0 - progress, 1.0), 1.0 - mix(progress, 0.0, persp), 0.0);\n\tvec2 toP = xskew(p / vec2(progress, 1.0), mix(pow(progress, 2.0), 1.0, persp), 1.0);\n\tif (inBounds(fromP)) {\n\t\treturn getFromColor(fromP);\n\t}\n\telse if (inBounds(toP)) {\n\t\treturn getToColor(toP);\n\t}\n\treturn bgColor(op, fromP, toP);\n}\nvoid main() {\n\tgl_FragColor = transition(vUv.xy);\n}\n"